/*------------------------------------*\
    #LAYOUT
\*------------------------------------*/

/**
 * Layout container
 */
 .l-frame, .alignwide {
	max-width: $l-max-width;
	margin: 0 auto;
  padding: 1rem; 
	
}

.alignfull {
	width: 100vw;
	position: relative;
	left: 50%;
	right: 50%;
	margin-left: -50vw;
	margin-right: -50vw;
}

/*------------------------------------*\
    #TEMPLATES
\*------------------------------------*/

/**
 * 2 column layout
 */
.l--two-col {
	display: flex;
	flex-direction: column;
	margin-top: $space-double;
	.l-main {
		padding-top: 1em;
		padding-left: $pad-double;
		padding-right: $pad-double;
		@media all and (min-width: $bp-large) {
			width: 58%;
			padding-right: $pad-double;
		}
		@media all and (min-width: $bp-large-2) {
			padding-right: $pad-triple;
		}
	}

	/**
	 * Sidebar
	 */
	.l-sidebar {
		
		@media all and (min-width: $bp-large) {
			flex-direction: column;
			width: 42%;
			padding: 0 $pad 0 $pad-double;
		}
		@media all and (min-width: $bp-large-2) {
			padding: 0 $pad 0 $pad-triple;
		}
	

	}
	
	@media all and (min-width: $bp-large) {
		flex-direction: row;
	}
	&.reverse {
		.l-main {
			@media all and (min-width: $bp-large) {
				padding-left: $pad-double;
			}
			@media all and (min-width: $bp-large-2) {
				padding-left: $pad-triple;
			}
		}
		.l-sidebar {
			@media all and (min-width: $bp-large) {

				padding: 0 $pad-double 0 $pad;
			}
			@media all and (min-width: $bp-large-2) {
				padding: 0 $pad-triple 0 $pad;
			}
		}

	}
}


/*------------------------------------*\
    #GRIDS
\*------------------------------------*/

/**
 * Grid
 */
.g {
	overflow: hidden;
	margin: 0 (-$pad-half);
}

/**
 * Grid item
 */
.gi {
	padding: $pad $pad-and-quarter;

	img {
		display: block;
	}

	@media all and (min-width: $bp-med) {
		float: left;
	}
}

/**
 * 2 up grid
 */
.g-2up {
	@media all and (min-width: $bp-med) {
		> .gi {
			float: left;
			width: 50%;

			&:nth-of-type(odd) {
				clear: left;
			}
		}
	}
}

/**
 * Grid side by side
 */
.g-half {
	> .gi {
		float: left;
		width: 50%;

		&:nth-of-type(odd) {
			clear: left;
		}
	}
}

/**
 * 3 up grid
 */
.g-3up {
	@media all and (min-width: $bp-med) {
		> .gi {
			float: left;
			width: 50%;

			&:nth-of-type(2n+1) {
				clear: left;
			}
		}
	}

	@media all and (min-width: $bp-large) {
		> .gi {
			width: 33.3333333%;

			&:nth-of-type(2n+1) {
				clear: none;
			}

			&:nth-of-type(3n+1) {
				clear: left;
			}
		}
	}
}

/**
 * 4 up grid
 */
.g-4up {
	@media all and (min-width: $bp-med) {
		>.gi {
			float: left;
			width: 50%;

			&:nth-of-type(2n+1) {
				clear: both;
			}
		}
	}

	@media all and (min-width: $bp-large) {
		>.gi {
			width: 25%;

			&:nth-of-type(2n+1) {
				clear: none;
			}

			&:nth-of-type(4n+1) {
				clear: left;
			}
		}
	}
}

/**
 * Quarter grid always displayed side by side
 */
.g-quarter {
	> .gi {
		float: left;
		width: 24%;

		&:nth-of-type(4n+1) {
			clear: left;
		}
	}
}

/**
 * Grid max 4 up
 */
.g-max4 {

	@media all and (min-width: $bp-small-2) {
		>.gi {
			float: left;
			width: 50%;

			&:nth-of-type(2n+1) {
				clear: both;
			}
		}
	}

	@media all and (min-width: $bp-small-3) {
		>.gi {
			width: 33.3333333%;

			&:nth-of-type(2n+1) {
				clear: none;
			}

			&:nth-of-type(3n+1) {
				clear: left;
			}
		}
	}

	@media all and (min-width: $bp-large) {
		>.gi {
			width: 25%;

			&:nth-of-type(3n+1) {
				clear: none;
			}

			&:nth-of-type(4n+1) {
				clear: left;
			}
		}
	}
}

/**
 * Grid max of 5 up
 */
.g-max5 {

	>.gi {
		float: left;
		width: 50%;

		&:nth-of-type(2n+1) {
			clear: both;
		}
	}

	@media all and (min-width: $bp-small-3) {
		>.gi {
			width: 33.3333333%;

			&:nth-of-type(2n+1) {
				clear: none;
			}

			&:nth-of-type(3n+1) {
				clear: left;
			}
		}
	}

	@media all and (min-width: $bp-med) {
		>.gi {
			width: 25%;

			&:nth-of-type(3n+1) {
				clear: none;
			}

			&:nth-of-type(4n+1) {
				clear: left;
			}
		}
	}

	@media all and (min-width: $bp-med-2) {
		>.gi {
			width: 20%;

			&:nth-of-type(4n+1) {
				clear: none;
			}

			&:nth-of-type(5n+1) {
				clear: left;
			}
		}
	}
}
